import  { Container } from './Progress.style.js';
import Like from '../../static/images/home/like.svg';

export default class Progress extends React.Component {

  constructor(props, context) {
    super(props, context);
  }

  render () {
    const  { percentages } = this.props;

    return (
      <Container progressOne={percentages[0]} progressTwo={percentages[1]}>
        <div class="progressOne">
          <Like />
          <p>{percentages[0]}</p>
        </div>
        <div class="progressTwo">
          <Like />
          <p>{percentages[1]}</p>
        </div>
      </Container>
    )
  }

}




